<template>
  <div class="rating-star clearfix">
    <div class="has-rating" v-if="lightStar>0">
      <ul>
        <!-- <li class="normal-star" :class="lightStar()"></li>
        <li class="normal-star" :class="lightStar()"></li>
        <li class="normal-star" :class="lightStar()"></li>
        <li class="normal-star" :class="lightStar()"></li>
        <li class="normal-star" :class="lightStar()"></li> -->
        <li v-for="n in lightStar" class="normal-star light-star"></li>
        <li v-for="n in grayStar" class="normal-star"></li>
      </ul>
    </div>
    <span v-else class="no-rating">暂无评分</span>
  </div>
</template>

<script>
export default {
  props: ['rating'],
  data () {
    return {
      lightStar: 0,
      grayStar: 0
    }
  },
  created () {
    this.lightStar = Math.round(this.rating/2),
    this.grayStar = 5-this.lightStar
  }
}
</script>

<style lang="scss" scoped>
.rating-star {
  ul {
    display: inline-flex;
    li {
      display: inline-block;
      margin: 0 1px;
    }
  }
  span {
    font-size: 12px;
    color: #aaaaaa;
  }
}
.normal-star {
  width: 12px;
  height: 12px;
  background-image: url(data:image/png;base64,);
  background-size: 12px 12px;
}
.light-star {
  background-image: url(data:image/png;base64,);
  background-size: 12px 12px;
}
</style>



